<template>
  <div class="Audio">
    <div class="audio-layout">
      <div class="audio-box">
        <vue-slider v-model="currentTrackDuration" :tooltip="'none'" />
        <button v-if="!isPlaying" class="play_button" v-on:click="playSong">
          <img alt="Vue logo" src="../assets/icon/play_circle.png" />
        </button>
        <button v-if="isPlaying" class="play_button" v-on:click="pauseSong">
          <img alt="Vue logo" src="../assets/icon/pause_circle.png" />
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import song from "../data/song.json";
import VueSlider from "vue-slider-component";
import "vue-slider-component/theme/antd.css";

export default {
  name: "Audio",
  components: {
    VueSlider
  },
  data() {
    return {
      loading: "getLoadingState",
      value1: 50,
      Songs: song,
      presentSongId: 0,
      lastSongId: 0,
      isPlaying: false,
      audio: new Audio(),
      isPaused: false,
      volume: 0.5,
      //
      timeLapse: false,
      timeBufferSecs: 0,
      timeBufferMins: 0,
      currentTrackTime: 0,
      lastRecordedTrackTime: -1,
      countCheck: 0,
      currentTrackDuration: 0,
      //
      color: "#8dff97",
      progressPercent: 0,
      continuousPlay: false
    };
  },
  methods: {
    playSong() {
      this.audio.src = this.Songs[2].audio;
      this.audio.play();
      this.isPlaying = true;
      this.isPaused = false;
      this.currentTrackTime = 0;
      this.view();
    },
    pauseSong() {
      this.isPlaying = false;
      this.audio.pause();
    },
    view() {
      let song = this;
      setInterval(function() {
        if (song.isPlaying) {
          song.currentTrackTime =
            (song.audio.currentTime / song.audio.duration) * 100;
          song.currentTrackDuration = song.currentTrackTime;
        }
      }, 500);
    }
  }
};
</script>

